<template>
  <div class="home_left_box">
    <!-- 关于我 -->
    <div class="about_me box" v-if="baseData.aboutMeModule.modulePermission">
      <h2>{{baseData.aboutMeModule.moduleTitle}}</h2>
      <ul>
        <i>
          <img :src="baseData.aboutMeModule.userAvatar" alt />
        </i>
        <p>
          <b>{{baseData.aboutMeModule.nckName}}</b>
          {{baseData.aboutMeModule.details}}
        </p>
      </ul>
    </div>
    <!-- 相册 -->
    <div class="photo_book box" v-if="baseData.photoBookModule.modulePermission">
      <h2>{{baseData.photoBookModule.moduleTitle}}</h2>
      <ul class="row float-l">
        <li v-for="(i,index) in baseData.photoBookModule.photoBookData.leftBookData" :key="index" v-show="i.modulePermission">
          <img
            :src="i.imageSrc+i.backgroundImageName+'.jpg'"
            @click.stop.prevent="select_photo(i.router,i.photoBookId,i.classPhotoBookId)"
            :alt="i.title"
          />
        </li>
      </ul>
    </div>
    <!-- 搜索 -->
    <div class="search_box" v-if="baseData.searchModule.modulePermission">
      <Input search enter-button="搜索" placeholder="请输入关键字词" class="input_text" />
    </div>
    <!-- 分类 -->
    <div class="classify_box box" v-if="baseData.classifyModule.modulePermission">
      <h2>{{baseData.classifyModule.moduleTitle}}</h2>
      <ul>
        <li
          v-for="(i,index) in baseData.classifyModule.data"
          :key="index"
        >{{i.classTitle}} （{{i.amount}}）</li>
      </ul>
    </div>
    <!-- 我的推荐 -->
    <div class="recommend box" v-if="baseData.recommendModule.modulePermission">
      <h2>{{baseData.recommendModule.moduleTitle}}</h2>
      <ul>
        <li v-for="(i,index) in baseData.recommendModule.recommendData" :key="index">
          <span>{{i.articleTitle}}</span>
        </li>
      </ul>
    </div>
    <!-- 链接 -->
    <div class="links box" v-if="baseData.linksModule.modulePermission">
      <h2>{{baseData.linksModule.moduleTitle}}</h2>
      <ul>
        <li v-for="(i,index) in baseData.linksModule.data" :key="index">{{i.linkTitle}}</li>
      </ul>
    </div>
    <!-- 关注我 -->
    <div class="follow_me box" v-if="baseData.followMeModule.modulePermission">
      <h2>{{baseData.followMeModule.moduleTitle}}</h2>
      <ul>
        <img :src="baseData.followMeModule.data[0].src" alt />
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      meShow: true,
    };
  },
  mounted() {},
  methods: {
    select_photo(router,id,classId) {
      this.$router.push({path: router, query: {id: id,classId:classId}});
      this.$emit('changeLeftShow')
    },
  },
};
</script>

<style lang="scss" scoped>
</style>


